/**
 * Universal finder
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/finder/finder
 * @author Web Development
 * @copyright City, University of London 2019
 */

import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { find } from '../finder/funnelback';
import MobileFilters from './components/filters/filtersmobile';
import Results from './components/results/results';
import scroll from 'zenscroll';
import { reduceMotion } from '../../util';
import replaceHistory from '../finder/logic/replace-history';
import { getNonFBParams, getFacetParams } from '../finder/logic/url-params';
import useHasMounted from '../finder/logic/has-mounted';

const oneSecond = 1000,
    scrollDuration = reduceMotion() ? 0 : oneSecond,
    screenOffsetRatio = 10;

/**
 * Launch the universal Finder.
 *
 * @param {object} props The JSON configuration file for the Finder.
 * @return {object} The React component to render.
 */
function DestinationLeaversHE(props) {
    /**
     * Check if react has mounted
     */
    const hasMounted = useHasMounted();
    // check if we in Matrix
    const matrixState = !hasMounted && props.initialQuery;
    const params = matrixState
            ? props.initialQuery
            : new URLSearchParams(window.location.search),
        initialResults = props.initialResults || {};
    const nonFBParams = props.config.facetLabels.filter(
        (paramFacet) => paramFacet.nonFBParam
    );

    /**
     * initial state for the Funnelback query, taken from URL parameters and
     * configuration
     **/
    const initialQuery = {
        collection: props.config.collection,
        facets:
            props.config.facetLabels.length > 0 && params
                ? getFacetParams(props.config.facetLabels, params, matrixState)
                : {},
        parameters:
            nonFBParams.length > 0 && params
                ? getNonFBParams(props.config.facetLabels, params, matrixState)
                : {},
        fixedFacets: props.config.fixedFacets,
        fixedParameters: props.config.fixParameters
            ? props.config.fixParameters
            : [],
        interacted: false,
        misspelling: null,
        numRanks:
            hasMounted && params.get('num_ranks')
                ? params.get('num_ranks')
                : props.config.numRanks,
        query:
            !matrixState && params.get('query')
                ? params.get('query')
                : params && params.query
                ? params.query
                : '',
        sortType:
            !matrixState && params.get('query')
                ? ''
                : !matrixState && params.get('sort')
                ? params.get('sort')
                : params && params.sort
                ? params.sort
                : props.config.sort[0].type,
        startRank:
            !matrixState && params.get('start_rank')
                ? params.get('start_rank')
                : params && params.start_rank
                ? params.start_rank
                : 1,
    };

    /**
     * Dummy, empty Funnelback response object for initial state.
     */
    const initialResponse =
        Object.keys(initialResults).length > 0
            ? Object.freeze(initialResults)
            : Object.freeze({
                  bestBets: [],
                  extraSearches: {},
                  facets: [],
                  results: [],
                  spell: null,
                  summary: {
                      currEnd: 0,
                      currStart: 0,
                      numRanks: 0,
                      totalMatching: 0,
                  },
              });

    // State objects for the Funnelback query and response
    const [query, setQuery] = useState(initialQuery);
    const [funnelbackResponse, setResponse] = useState(initialResponse);

    // Boolean to indicate when a query is in progress
    const [updating, setUpdating] = useState(true);

    // Request token from the Funnelback request object, so we can cancel if
    // another request is triggered by the user
    const [call, setCall] = useState({
        cancel: () => {},
    });

    // useEffect doesn't deep inspect objects, so we need an additional, plain
    // state variable to indicate that the query state has changed and the
    // component should render
    // the value isn't important, it's just easy to toggle a bool back and forth
    const [update, setUpdate] = useState(false);

    // Retrieve Funnelback results
    useEffect(() => {
        // preserve the state
        replaceHistory(
            query.query,
            query.startRank,
            query.facets,
            query.parameters,
            query.sortType,
            props.config.facetLabels,
            props.config.sort[0].type,
            hasMounted
        );

        // indicate a request is in progress
        setUpdating(true);

        query.interacted &&
            scroll.center(
                props.element.querySelector('.finder__results'),
                scrollDuration,
                -window.innerHeight / screenOffsetRatio
            );

        /**
         * cancel any request already in progress
         *
         * async requests can return out of order
         */
        call.cancel();

        // make a new, asynchronous request to Funnelback
        const [request, requestToken] = find(
            query.collection,
            query.fixedFacets,
            query.fixedParameters,
            query.query,
            query.sortType,
            query.startRank,
            query.numRanks,
            query.facets,
            query.parameters
        );

        // save the requestToken
        setCall({
            cancel: () => {
                requestToken.cancel();
            },
        });

        // when the response from Funnelback arrives,
        // update the results and display them
        request
            .then((data) => {
                setResponse(data);
                setUpdating(false);

                if (data.spell && data.summary.totalMatching === 0) {
                    const newQueryText = data.spell.text.split(/\|/)[0].trim(),
                        newQuery = query;

                    newQuery.misspelling = query.query;
                    newQuery.query = newQueryText;
                    newQuery.startRank = 1;
                    setQuery(newQuery);
                    setUpdate(!update);
                }
            })
            .catch(() => {
                setResponse(initialResponse);
                setUpdating(false);
            });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [update]);

    // update props so child components can update the query
    const updater = {
        query: (newQuery) => setQuery(newQuery),
        results: (newUpdate) => setUpdate(newUpdate),
        updateState: update,
    };

    /**
     * @param  {boolean} resetSort - Reset the sort order too?
     */
    const clear = (resetSort) => {
        const newQuery = query;

        call.cancel();

        newQuery.sortType = resetSort
            ? props.config.sort[0].type
            : newQuery.sortType;
        newQuery.facets = {};
        newQuery.startRank = 1;
        newQuery.misspelling = null;
        newQuery.interacted = true;

        setQuery(newQuery);
        setUpdate(!update);
    };

    return (
        <form
            className={
                props.config.facetLabels.length > 0
                    ? 'finder'
                    : 'finder finder--nofilters'
            }
            onSubmit={(e) => {
                e.preventDefault();
            }}
        >
            {props.config.facetLabels.length > 0 && (
                <React.Fragment>
                    <MobileFilters
                        config={props.config}
                        query={query}
                        response={funnelbackResponse}
                        update={updater}
                        updating={updating}
                        summariseAs={props.config.summariseAs}
                        clear={clear}
                        matrixState={matrixState}
                    />
                </React.Fragment>
            )}
            <div className="wrapper--destination-leavers-HE__info-text">
                <p>
                    If you do not see the course you are interested in, please
                    contact the{' '}
                    <a href="/prospective-students/career-development/careers-team">
                        careers department
                    </a>{' '}
                    at City for full details
                </p>
            </div>
            <Results
                clear={clear}
                config={props.config}
                query={query}
                response={funnelbackResponse}
                summariseAs={props.config.summariseAs}
                type={props.config.resultCard}
                update={updater}
                updating={updating}
                matrixState={matrixState}
            />
        </form>
    );
}

DestinationLeaversHE.propTypes = {
    config: PropTypes.object,
    element: PropTypes.object,
    initialQuery: PropTypes.object,
    initialResults: PropTypes.object,
};

export default DestinationLeaversHE;
