import React from 'react';
import PropTypes from 'prop-types';
import Charts from './chart';
import Company from './companys';
import Institutions from './institutions';
import Job from './job';
import Salary from './salary';
import PositiveOutcome from './positiveOutcome';
import GradLevel from './grad-level';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Generic(props) {
    const company = props.details.listMetadata.companys ? (
            <Company text={props.details.listMetadata.companys[0]} />
        ) : null,
        institutions = props.details.listMetadata.institutions ? (
            <Institutions text={props.details.listMetadata.institutions[0]} />
        ) : null,
        jobT = props.details.listMetadata.jobT ? (
            <Job text={props.details.listMetadata.jobT[0]} />
        ) : null,
        salary = props.details.listMetadata.salary ? (
            <Salary text={props.details.listMetadata.salary[0]} />
        ) : null,
        gradLevel = props.details.listMetadata.gradLevel ? (
            <GradLevel text={props.details.listMetadata.gradLevel[0]} />
        ) : null,
        positiveOutcome = props.details.listMetadata.positiveOutcome ? (
            <PositiveOutcome
                text={props.details.listMetadata.positiveOutcome[0]}
            />
        ) : null;
    const charts = props.details.listMetadata.chart
        ? props.details.listMetadata.chart.map((d) => JSON.parse(d))
        : null;

    let count = 0;
    props.details.listMetadata.companys && count++;
    props.details.listMetadata.institutions && count++;
    props.details.listMetadata.jobT && count++;
    props.details.listMetadata.salary && count++;
    props.details.listMetadata.gradLevel && count++;
    props.details.listMetadata.positiveOutcome && count++;

    return (
        <div className="destination-leaver">
            <div className="card__details__text">
                <h2 className="card__heading">
                    Showing data for {props.details.title}
                </h2>
                <p className="card__description">
                    {props.details.listMetadata.c &&
                        props.details.listMetadata.c[0]}
                </p>
            </div>
            <div className="chart-row chart-row--columns">
                {charts
                    ? charts.map((c, i) => {
                          let k = `${c[0].title}${i}${c[0].id}`;
                          let chartsLength = charts.length;
                          return (
                              <Charts
                                  key={k}
                                  data={c[0].chart}
                                  title={c[0].title}
                                  id={c[0].id}
                                  chartsLength={chartsLength}
                                  chartId={parseInt(c[0].id)}
                              />
                          );
                      })
                    : null}
            </div>
            <div className="destination-leaver-content" data-length={count}>
                {salary}
                {company}
                {institutions}
                {jobT}
                {gradLevel}
                {positiveOutcome}
            </div>
        </div>
    );
}

Finder__Results__Generic.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Generic;
